<template>
<div id="app">
	<transition name="fade" mode="out-in">
		<router-view></router-view>
	</transition>
</div>
</template>

<script>
	export default {
		name: 'app',
		mounted()
		{
			window.cordovaBack = () => {
				try
				{
					if(location.href.endsWith('Dashboard'))
					{
						navigator.app.exitApp();
					}
					else
					{
						this.$router.push({name : 'Dashboard'});
					}
				}
				catch(err)
				{
					console.log(err);
				}
			}
		}
	}
</script>

