export default class Utils
{
	static getAppName()
	{
		return 'CardShop_Mobile';
	}

	static getBaseUrl()
	{
		//return 'http://localhost:9393/api/';
		return 'https://CardShopapitest.azurewebsites.net/api/';
	}

	static remeberMe(userId, password)
	{
		sessionStorage.setItem(Utils.getAppName() +'_userId', userId);
		sessionStorage.setItem(Utils.getAppName() +'_password', password);
	}

	static clearRemeberMe()
	{
		sessionStorage.removeItem( Utils.getAppName() +'_userId');
		sessionStorage.removeItem( Utils.getAppName() +'_password');
	}

	static getUserToken()
	{

		let tokenString = sessionStorage.getItem(Utils.getAppName() + '_userToken') || '';
		if(tokenString.length == 0)
		{
			return {
				UserId: '',
				Password: '',
				Ticks: '0',
				Secret: '',
				EncryptPassword: true,
				TokenID: '70d3fa69-4d3d-4cb7-882e-f3e97fa0352d',
				Settings: '',
				CompressType: ''
			};
		}

		let token = JSON.parse(tokenString);
		return {
			UserId: token.UserId,
			Password: token.Password,
			Ticks: token.Ticks,
			Secret: token.Secret,
			EncryptPassword: true,
			TokenID: '70d3fa69-4d3d-4cb7-882e-f3e97fa0352d',
			Settings: '',
			CompressType: ''
		};
	}

	static getCustomer()
	{

		let customerString = sessionStorage.getItem(Utils.getAppName() + '_customer') || '';
		return JSON.parse(customerString);
	}

	static setUserToken(userId, passwordHash, ticks, secret, email, customerPK, discountRate)
	{
		let token = {
			UserId: userId,
			Password: passwordHash,
			Ticks: ticks,
			Secret: secret
		};

		let customer = {
			Email: email,
			CustomerPK: customerPK,
			DiscountRate: discountRate
		};
		sessionStorage.setItem(Utils.getAppName() + '_userToken', JSON.stringify(token));
		sessionStorage.setItem(Utils.getAppName() + '_customer', JSON.stringify(customer));
	}

	static clearUserToken()
	{
		sessionStorage.removeItem(Utils.getAppName() + '_userToken');
		sessionStorage.removeItem(Utils.getAppName() + '_customer');
	}

	static isUserTokenExist()
	{
		let userId = sessionStorage.getItem(Utils.getAppName() + '_userToken') || '';
		return userId.length > 0;
	}

	static createUserToken(userId, passwordHash, ticks)
	{
		return {
			UserId: userId,
			Password: passwordHash,
			Ticks: ticks,
			EncryptPassword: true,
			TokenID: '70d3fa69-4d3d-4cb7-882e-f3e97fa0352d',
			Settings: '',
			CompressType: ''
		};
	}

	static getTicks()
	{
		let epochTicks = 621355968000000000;
		let ticksPerMillisecond = 10000;
		return epochTicks + (new Date().getTime() * ticksPerMillisecond);
	}

	static encryptPassword(ticks, value)
	{
		let array = new Array();
		for (let i = 0; i < value.length; i++)
		{
			if (i < ticks.length)
			{
				let ret = value.charCodeAt(i) + ticks.charCodeAt(i);
				array.push(ret.toString());
			}
			else
			{
				let ret = value.charCodeAt(i) + 18;
				array.push(ret.toString());
			}
		}

		return array.join(' ') + ' ';
	}

	static base64ToByteArray(base64)
	{
		let binary = atob(base64);
		let array = new Uint8Array(binary.length);
		for( var i = 0; i < binary.length; i++ )
		{
			array[i] = binary.charCodeAt(i);
		}
		return array;
	}
}
