import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    user: {},
    productList:[],
    stateList:[],
    payMethodList:[],
    shipMethodList:[],
    billingAddressList:[],
    cart: {},
    creditCard: {},
    paymentResult:{}
  },
  mutations: {
    setUser(state, user){
			state.user = user;
    },
    setProductList(state, list){
			state.productList = list;
    },
    setStateList(state, list){
			state.stateList = list;
    },
    setPayMethodList(state, list){
			state.payMethodList = list;
    },
    setShipMethodList(state, list){
			state.shipMethodList = list;
    },
    setBillingAddressList(state, list){
			state.billingAddressList = list;
    },
    setCart(state, cart){
			state.cart = cart;
    },
    setCreditCard(state, card){
			state.creditCard = card;
    },
    setPaymentResult(state, result){
			state.paymentResult = result;
    }
  },
  actions: {

  }
})
