import Vue from 'vue'
import Router from 'vue-router'
import Utils from './utils';

const Login = () => import(/* webpackChunkName: "Login" */'./views/Login');
const Register = () => import(/* webpackChunkName: "Login" */'./views/Register');
const ResetPassword = () => import(/* webpackChunkName: "Login" */'./views/ResetPassword');

const Layout = () => import(/* webpackChunkName: "Product" */'./views/Layout');
const Dashboard = () => import(/* webpackChunkName: "Product" */'./views/Dashboard');
const ProductList = () => import(/* webpackChunkName: "Product" */'./views/ProductList');
const Cart = () => import(/* webpackChunkName: "Product" */'./views/Cart');
const CartItem = () => import(/* webpackChunkName: "Product" */'./views/CartItem');

const PayShipping = () => import(/* webpackChunkName: "Pay" */'./views/PayShipping');
const PayBilling = () => import(/* webpackChunkName: "Pay" */'./views/PayBilling');
const PayMonthly = () => import(/* webpackChunkName: "Pay" */'./views/PayMonthly');
const PayConfirm = () => import(/* webpackChunkName: "Pay" */'./views/PayConfirm');
const PaySuccess = () => import(/* webpackChunkName: "Pay" */'./views/PaySuccess');

const HistoryList = () => import(/* webpackChunkName: "History" */'./views/HistoryList');
const HistoryDetail = () => import(/* webpackChunkName: "History" */'./views/HistoryDetail');

const Profile = () => import(/* webpackChunkName: "Profile" */'./views/Profile');
const ChangeCustomer = () => import(/* webpackChunkName: "Profile" */'./views/ChangeCustomer');
const ChangePassword = () => import(/* webpackChunkName: "Profile" */'./views/ChangePassword');

Vue.use(Router)

const router = new Router({
  routes:[
    {
      path :'/Login',
      name :'Login',
      component: Login
    },
    {
      path: '/Register',
      name: 'Register',
      component: Register
    },
    {
      path: '/ResetPassword',
      name: 'ResetPassword',
      component: ResetPassword
    },
    {
      path: '',
      name: 'Layout',
      component: Layout,
      redirect: '/Dashboard',
      children:[
        {
          path :'/Dashboard',
          name :'Dashboard',
          component: Dashboard
        },
        {
          path: '/ProductList',
          name: 'ProductList',
          component: ProductList
        },
        {
          path: '/Cart',
          name: 'Cart',
          component: Cart
        },
        {
          path: '/CartItem/:OrderItemPK',
          name: 'CartItem',
          component: CartItem
        },
        {
          path: '/PayShipping',
          name: 'PayShipping',
          component: PayShipping
        },
        {
          path: '/PayBilling',
          name: 'PayBilling',
          component: PayBilling
        },
        {
          path: '/PayMonthly',
          name: 'PayMonthly',
          component: PayMonthly
        },
        {
          path: '/PayConfirm',
          name: 'PayConfirm',
          component: PayConfirm
        },
        {
          path: '/PaySuccess',
          name: 'PaySuccess',
          component: PaySuccess
        },
        {
          path: '/HistoryList',
          name: 'HistoryList',
          component: HistoryList
        },
        {
          path: '/HistoryDetail/:OrderNumber',
          name: 'HistoryDetail',
          component: HistoryDetail
        },
        {
          path: '/Profile',
          name: 'Profile',
          component: Profile
        },
        {
          path: '/ChangeCustomer',
          name: 'ChangeCustomer',
          component: ChangeCustomer
        },
        {
          path: '/ChangePassword',
          name: 'ChangePassword',
          component: ChangePassword
        }
      ]
    }
  ]
});

router.beforeEach((to, from, next)=>{
  if(to.path == '/Register' || to.path == '/ResetPassword')
  {
    next();
  }
  else
  {
      if (to.path == '/login') {
        Utils.clearUserToken();
      }
      if (!Utils.isUserTokenExist() && to.path != '/login') {
        next({ path: '/login' });
      } else {
        next();
      }
  }
});

export default router;

